.countryDetails {
    height: 90px;
    width: 84%;
    border-bottom: 1px solid lightgray;
    margin: auto;
    padding-bottom: 1em !important;
    margin-top: 2.8em !important;
}

.country-icon {
    padding: 0 15px;
    max-width: 16%;
    min-width: 13%;
    float: left;
}

.country-flag {
    max-width: 100%;
    height: 100%;
}

.cases-box{
    text-align: center;
}

.cases-details {
    height: 100%;
    width: calc(100% - 16%);
    float: left;
    display: grid;
    grid-template-columns: 33.333% 47.33% calc(100% - 47.33% - 33.33%);
}

a {
    color: #1644ba;
    text-decoration: none;
    background-color: transparent;
    font-size: 1.2rem;
    font-family: Nunito;
}

p {
    color: #b5b5b5;
    font-weight: 400;
    font-size: 1rem;
    font-family: "Nunito";
}

p strong {
    color: #3c3c3c;
}